import "core-js/modules/es.array.push.js";
import { Notify } from 'vant';
import { signin, signup, verifCode } from "@/api/login.js";
import { hasEmaty } from "@/utils/tool";
import SiHeader from "@/components/SimpleHeader";
import { defineComponent } from "vue";
import head_m from "@/assets/images/head_m.png";
import head_g from "@/assets/images/head_g.png";
import { isMobile } from "@/utils/tool";
export default defineComponent({
  name: "UserLogin",
  data() {
    return {
      activeIndex: 0,
      username: "cs1",
      passwd: "1",
      verToken: "",
      verImg: "",
      userCodeVal: ""
    };
  },
  components: {
    SiHeader
  },
  methods: {
    changeTab(index) {
      this.activeIndex = index;
    },
    signin() {
      let params = {
        name: this.username,
        passwd: this.passwd
      };
      if (hasEmaty(params)) {
        return;
      }
      if (this.verToken != this.userCodeVal) {
        Notify('验证码不正确');
        return;
      }
      signin(params).then(res => {
        localStorage.setItem("token", res.token);
        localStorage.setItem("userInfo", JSON.stringify(res.userInfo));
        let defaultHeadurl = "";
        if (res.userInfo.headurl) {
          defaultHeadurl = this.$config.baseUrl + res.userInfo.headurl;
        } else {
          defaultHeadurl = res.userInfo.sex == "男" ? head_m : head_g;
        }
        localStorage.setItem("userHead", defaultHeadurl);
        this.$router.push({
          path: "/index"
        });
      });
    },
    signup() {
      let params = {
        name: this.username,
        passwd: this.passwd
      };
      if (hasEmaty(params)) {
        return;
      }
      signup(params).then(() => {
        this.activeIndex = 0;
      });
    },
    getVerifCode() {
      verifCode().then(res => {
        this.verImg = `data:image/jpeg;base64,${res.base64}`;
        this.verToken = res.token;
      });
    }
  },
  mounted() {
    if (!isMobile()) {
      Notify({
        type: 'danger',
        message: "请打开移动端预览模式 Ctrl + Shift + M"
      });
    }
    this.getVerifCode();
  }
});